.App {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background: url('https://images.pexels.com/photos/2336/trees-grass-lawn-park.jpg?cs=srgb&dl=autumn-fall-golf-2336.jpg&fm=jpg');
  background-repeat: no-repeat;
  background-size: cover;
}
.Counter {
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    width: 100%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Counter > h1 {
    font-size: 40px;
    color: white;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
    margin-bottom: 0;
}

.Counter > h2 {
    font-size: 20px;
    color: white;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.Counter > h3 {
    font-size: 14px;
    font-style: italic;
    color: white;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.Swan {
    position: absolute;
}

.Swan img {
    width: 100px;
    height: 60px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.Explainer {
    font-family: Arial, Helvetica, sans-serif;
    padding: 0 20px 20px 20px;
}

.Explainer p {
    font-size: 14px;
    line-height: 26px;
}

.Explainer small {
    margin-top: 40px;
    display: block;
    font-size: 12px;
    font-style: italic;
}
